import React, { useEffect, useState } from "react";
import "./App.css"; // Assuming your styles are in this file.
import logo from "./images/logo.png";
import bud from "./images/bud.png";
import icon from "./images/icon.png";
import popupemily from "./images/popupemily.png";
import manonbeach from "./images/manonbeach.png";
import emilyBackground from "./images/emilyBackground.jpg";
import audio from "./images/audio.gif";
import voice from "./images/voice.gif";
import CurvedSeparator from "./CurvedSeparator";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faRobot,
  faBrain,
  faMapMarkerAlt,
  faHotel
} from "@fortawesome/free-solid-svg-icons";
import {
  faComments,
  faTasks,
  faFilter,
  faCalendarCheck
} from "@fortawesome/free-solid-svg-icons";

const PersonaClient = ({ shouldStartPersona, onReady, onLimitReached }) => {
  const [personaClient, setPersonaClient] = useState(null);
  const [didStartPersona, setDidStartPersona] = useState(false);

  useEffect(() => {
    const script = document.createElement("script");
    script.src =
      "https://app.sindarin.tech/PersonaClientPublic?apikey=430c62b9-d492-4ebc-8a2f-5a6aa40920fb";

    script.addEventListener("load", async () => {
      const apiKey = "430c62b9-d492-4ebc-8a2f-5a6aa40920fb";
      const personaClient = new window.PersonaClient(apiKey);
      setPersonaClient(personaClient);
    });
    document.head.appendChild(script);
  }, []);

  useEffect(() => {
    const getUserID = async () => {
      return "user-" + Math.random().toString(36).substr(2, 9);
    };

    if (personaClient && shouldStartPersona && !didStartPersona) {
      setDidStartPersona(true);

      const character = "Emily";
      getUserID().then((userId) => {
        personaClient
          .init(userId, character)
          .then(() => {
            personaClient.on("ready", () => {
              onReady();
            });
          })
          .catch((err) => {
            console.log("personaClient init error", err);
            if (/You have/gi.test(err)) {
              onLimitReached();
            }
          });
      });
    }
  }, [
    personaClient,
    shouldStartPersona,
    onReady,
    didStartPersona,
    onLimitReached
  ]);

  return null;
};

const HeaderComponent = () => {
  // This function is called when the hamburger menu is clicked
  const handleHamburgerClick = () => {
    const navMenu = document.getElementById("navMenu");
    navMenu.classList.toggle("active"); // Toggles the 'active' class to show/hide the menu
  };

  return (
    <header>
      <img src={logo} alt="company logo" id="logo" />

      {/* Add the hamburger menu here */}
      <div className="hamburger-menu" onClick={handleHamburgerClick}>
        <div className="bar"></div>
        <div className="bar"></div>
        <div className="bar"></div>
      </div>

      {/* The navigation menu itself */}
      <nav id="navMenu">
        <ul>
          <li>
            <a href="/jane">Jane</a>
          </li>
          <li>
            <a href="/emily">Emily</a>
          </li>
          <li>
            <a href="/peter">Peter</a>
          </li>
        </ul>
      </nav>
    </header>
  );
};

const HeroComponent = ({ onButtonClick }) => {
  const handleButtonClick = () => {
    // Toggle blur on the entire content of the page
    const contentWrapper = document.querySelector(".page-content-wrapper");
    if (contentWrapper) {
      contentWrapper.classList.toggle("blurred");
    }

    // Toggle popup display
    const popup = document.getElementById("EmilyPopup");
    if (popup) {
      if (popup.style.display === "none" || !popup.style.display) {
        popup.style.display = "block";
      } else {
        popup.style.display = "none";
      }
    }

    onButtonClick();
  };

  return (
    <section
      className="hero"
      style={{ backgroundImage: `url(${emilyBackground})` }}
    >
      <img src={bud} alt="bud" id="bud" />
      <div className="hero-text-wrapper">
        <div className="action">
          <h1>
            Discover the World with <span>Emily</span>
          </h1>
          <p>Your voice-activated AI travel companion;</p>
          <p>Speak your dream, discover the destination.</p>
          <button className="emily-button" onClick={handleButtonClick}>
            <img src={icon} alt="Icon" id="buttonIcon" />
            Speak to Emily
          </button>
        </div>
      </div>

      {/* Popup reintroduced */}
      <div id="EmilyPopup" className="popup" style={{ display: "none" }}>
        <div className="sound-wave">
          <div className="wave"></div>
          <div className="wave"></div>
          <div className="wave"></div>
          <div className="wave"></div>
          <div className="wave"></div>
        </div>
        You are now speaking to Emily.
        <p className="microphone-note">
          (Please make sure you are using Google Chrome and you enable your
          microphone)
        </p>
      </div>

      <CurvedSeparator />
    </section>
  );
};

const InfoBox = ({ title, description }) => (
  <div className="info-box">
    <img src="./images/icon.png" alt="" />
    <h3>{title}</h3>
    <p>{description}</p>
  </div>
);

const InfoSection = () => {
  return (
    <section className="info">
      {/* You can add your h1 and p tags here if needed */}
      <h1>Check out the key features of Neural Voice:</h1>
      <p>
        Your voice activated Travel Agent will help your customers find their
        dream holiday
      </p>

      <div className="info-flex">
        <InfoBox
          title={
            <>
              <FontAwesomeIcon icon={faRobot} size="lg" />
              Intelligent Chat
            </>
          }
          description="Engage in intuitive conversations with Neural Voice."
        />
        <InfoBox
          title={
            <>
              <FontAwesomeIcon icon={faBrain} size="lg" />
              Ideal Questions
            </>
          }
          description="AI-driven queries for personalised holiday experiences."
        />
        <InfoBox
          title={
            <>
              <FontAwesomeIcon icon={faMapMarkerAlt} size="lg" />
              Perfect Location
            </>
          }
          description="Deliver destinations tailored to your customers preferences."
        />
        <InfoBox
          title={
            <>
              <FontAwesomeIcon icon={faHotel} size="lg" />
              Easy Booking
            </>
          }
          description="Seamless hotel suggestions and booking assistance for everybody."
        />
      </div>
    </section>
  );
};

const InfoDump = () => {
  return (
    <section className="info-dump">
      <div className="content">
        <h1>Your AI Travel Guide: Dream, Explore, Book</h1>
        <p>
          Embark on a transformative travel journey with Neural Voice. More than
          an agent, it’s your intuitive partner in crafting dream holidays. By
          engaging in dynamic conversations, it discovers not just where you
          want to go, but why.
        </p>
        <p>
          Neural Voice isn’t confined to answering—it asks, learns, and
          inspires. From serene beaches to vibrant cities, it deciphers your
          wanderlust, guiding you to destinations you'll adore.
        </p>
        <p>
          And when dreams become decisions, it effortlessly transitions you to
          booking, ensuring your imagined adventures become tangible
          itineraries. With Neural Voice, travel becomes an emotional
          exploration, not just a destination. Begin your journey.
        </p>
      </div>
      <div className="travel-image-box">
        <img
          src={manonbeach}
          alt="Man on Beach"
          className="travel-beach-image"
        />
      </div>
    </section>
  );
};

const FooterComponent = () => (
  <footer>
    <div className="reach">{/* ...remaining footer code... */}</div>
    <p id="rights">All Rights Reserved &copy; 2023 Neural Voice</p>
  </footer>
);

const App = () => {
  const [shouldStartPersona, setShouldStartPersona] = useState(false);
  const [showPopup, setShowPopup] = useState(false); // This state will control the popup's visibility.

  const handleButtonClick = () => {
    setShowPopup(true); // show the popup
    setShouldStartPersona(true);
  };

  return (
    <div className="App">
      <svg width="0" height="0" style={{ position: "absolute" }}>
        <defs>
          <clipPath id="curvedPath" clipPathUnits="objectBoundingBox">
            <path d="M 0,0 L 1,0 L 1,0.7 Q 0.5,1 0,0.8.5 Z" />
          </clipPath>
        </defs>
      </svg>
      <div className={`page-content-wrapper ${showPopup ? "blurred" : ""}`}>
        <HeaderComponent />
        <HeroComponent onButtonClick={handleButtonClick} />
        <InfoSection />
        <InfoDump />
        <FooterComponent />
      </div>

      {/* Blurred overlay */}
      {showPopup && <div className="blur-overlay"></div>}
      {showPopup && (
        <div id="EmilyPopup" className="popup">
          <button
            className="close-btn"
            onClick={() => window.location.reload()}
          >
            X
          </button>

          <div className="popup-content-wrapper">
            <div className="popup-content">
              <h2>
                You're now chatting with <span>Emily</span>{" "}
                <img src={voice} alt="Voice" id="Voice" />
              </h2>

              <p className="microphone-note">
                (Please make sure you are using Google Chrome and you enable
                your microphone)
              </p>
            </div>
            <img src={popupemily} alt="Emily" id="PopupEmily" />
          </div>
        </div>
      )}

      <PersonaClient
        shouldStartPersona={shouldStartPersona}
        onReady={() => {
          console.log("Persona ready!");
        }}
        onLimitReached={() => console.log("Limit reached!")}
      />
    </div>
  );
};

export default App;
